body {
  font-family: Arial, sans-serif;
}

.container {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
}

.modules, .professors {
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  width: 200px;
}

.module, .professor {
  background-color: #f0f0f0;
  border: 1px solid #999;
  border-radius: 3px;
  cursor: pointer;
  margin-bottom: 10px;
  padding: 10px;
}

.module.dragging, .professor.dragging {
  opacity: 0.5;
}

.module.hovered {
  background-color: #e0e0e0;
}
